<template>
  <div>
    <div class="navBar" style="margin-bottom:1.9rem">
      <van-nav-bar title="" left-text="" left-arrow right-text="" fixed @click-left="toBackOrder" @click-right="toOrder"></van-nav-bar>
    </div>
    <div>
      <van-radio-group v-model="payType" @change="changeType">
        <van-cell-group>
          <van-cell title="" clickable @click="payType = ''">
            <van-radio name=""/>
          </van-cell>
          <van-cell title="" clickable @click="payType = ''">
            <van-radio name=""/>
          </van-cell>
          <van-cell title="" clickable @click="payType = ''">
            <van-radio name=""/>
          </van-cell>
        </van-cell-group>
      </van-radio-group>
    </div>
    <div class="bottom_btn" @click="sureOrder">
      <van-button type="danger" size="large"><span>{{payType}}</span><span>{{totalPrice | moneyFilter}}</span>
      </van-button>
    </div>
    <van-dialog v-model="show" show-cancel-button :before-close="beforeClose" @confirm="confirm">
      <van-field v-model="password" type="password" label="" placeholder=""/>
    </van-dialog>
  </div>
</template>
<script>
  import axios from 'axios'
  import url from '@/serviceAPI.config.js'
  import {toMoney} from '@/filter/moneyFilter.js'
  import {Dialog} from 'vant';
  import {Toast} from 'vant';

  var uuid = require('node-uuid');
  export default {
    data(){
      return {
        payType : '',
        totalPrice : '',
        show : false,
        password : '',
        orderData : [],
        orderID : ''
      }
    },
    created(){
      this.orderData = this.$route.params.data
      this.totalPrice = this.$route.params.totalPrice / 100
      this.orderID = this.$route.params.id
    },
    filters : {
      moneyFilter(money){
        return toMoney(money)
      }
    },
    methods : {
      beforeClose(action, done){
        if(action === 'confirm'){
          setTimeout(done, 1000);
        } else {
          done();
        }
      },
      changeType(name){
        console.log(name);
        this.payType = name
      },
      toOrder(){
        this.$router.push({name : 'orderList'})
      },
      toBackOrder(){
        Dialog.confirm({
          title : '?',
          message : '20'
        }).then(() =>{
          if(!this.orderID){
            let status = '';
            this.insertOrder(status);
          }
          this.toOrder()
        }).catch(() =>{
        });
      },
      confirm(){
        let status = '';
        if(!this.orderID){
          this.insertOrder(status);
        } else {
          this.updateOrder(this.orderID, status);
        }
        this.toOrder()
      },
      sureOrder(){
        this.show = true
      },
      updateOrder(id, status){
        axios({
          url : url.updateOrder,
          method : 'post',
          data : {id : id, status : status}
        }).then(response =>{
          if(response.data.code == 200 && response.data.message){
          } else {
            Toast('')
          }
        }).catch(error =>{
          console.log(error)
        })
      },
      insertOrder(status){
        let orderId = uuid.v1().replace(/-/g, '');
        axios({
          url : url.insertOrder,
          method : 'post',
          data : {id : orderId, status : status, price : this.totalPrice, orderData : this.orderData}
        }).then(response =>{
          if(response.data.code == 200 && response.data.message){
            console.log(response.data.message);
          } else {
            Toast('')
          }
        })
          .catch(error =>{
            console.log(error)
          })
      }
    }
  }
</script>
<style scoped>
  .bottom_btn{
    position:fixed;
    bottom:0;
    left:0;
    width:100%;
  }
</style>



